'use client';

import Image from 'next/image';
import Link from 'next/link';
import { Button } from '@/components/ui/button';
import { Plus } from 'lucide-react';
import { ProdukResto } from '@/types';
import { formatRupiah } from '@/lib/format';

type Props = {
    product: ProdukResto;
    onAddToCart?: (product: ProdukResto) => void;
};

export default function ProductCard({ product, onAddToCart }: Props) {
    return (
        <div className="group bg-white rounded-[2rem] p-3 border border-gray-100 hover:border-[#D93F21]/30 hover:shadow-xl transition-all duration-300 hover:-translate-y-1 relative overflow-hidden flex flex-col h-full">
            <Link href={`/pelanggan/produk/${product.id}`} className="cursor-pointer block relative">
                <div className="relative h-48 w-full rounded-[1.5rem] overflow-hidden mb-3 bg-gray-100 shadow-inner">
                    <Image 
                        src={product.foto_url || '/images/placeholder-food.jpg'} 
                        alt={product.nama_produk}
                        fill
                        className="object-cover group-hover:scale-110 transition-transform duration-700"
                        unoptimized
                    />
                    <div className="absolute top-3 left-3 bg-white/90 px-3 py-1.5 rounded-full text-[10px] font-bold text-gray-800 shadow-sm z-10 uppercase tracking-wide backdrop-blur-sm">
                        {product.kategori?.nama_kategori}
                    </div>
                </div>
            </Link>

            <div className="px-2 pb-2 flex flex-col flex-1">
                <Link href={`/pelanggan/produk/${product.id}`} className="cursor-pointer block flex-1">
                    <h3 className="font-extrabold text-gray-900 text-lg mb-1 line-clamp-1 group-hover:text-[#D93F21] transition-colors">
                        {product.nama_produk}
                    </h3>
                    <p className="text-gray-400 text-xs line-clamp-2 min-h-[2.5em] leading-relaxed">
                        {product.deskripsi || "Menu lezat spesial untuk Anda..."}
                    </p>
                </Link>

                <div className="flex justify-between items-center pt-4 border-t border-gray-50 mt-4">
                    <p className="text-lg font-extrabold text-gray-900">{formatRupiah(Number(product.harga_jual))}</p>

                    <Button 
                        size="sm" 
                        className="bg-white text-[#D93F21] border border-[#D93F21] hover:bg-[#D93F21] hover:text-white rounded-full font-bold transition-all h-9 px-4 z-20 relative shadow-sm hover:shadow-md"
                        onClick={(e) => { e.stopPropagation(); e.preventDefault(); onAddToCart?.(product); }}
                    >
                        Order <Plus className="w-3 h-3 ml-1" />
                    </Button>
                </div>
            </div>
        </div>
    );
}
