'use client';

import { useState } from 'react';
import Image from 'next/image';
import { Receipt, User, ShoppingCart, Trash2, Minus, Plus, Loader2, ArrowRight } from 'lucide-react';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Separator } from '@/components/ui/separator';
import { useCart } from '@/context/CartContext';
import { useAuth } from '@/hooks/useAuth';
import { useRouter } from 'next/navigation';
import { formatRupiah } from '@/lib/format';
import { toast } from 'sonner';

export default function OrderSidebar() {
    const { user } = useAuth({ middleware: 'auth' });
    const router = useRouter();
    const { cart, pendingCart, restorePendingCart, discardPendingCart, removeFromCart, updateQty, totalItems, totalPrice } = useCart();

    const [tableNumber, setTableNumber] = useState('');
    const [isProcessing, setIsProcessing] = useState(false);

    const handleOrderNow = async () => {
        if (cart.length === 0) return toast.error('Keranjang masih kosong!');
        if (!tableNumber) return toast.error('Mohon isi Nomor Meja!');
        setIsProcessing(true);
        // small UX delay to indicate processing
        setTimeout(() => {
            setIsProcessing(false);
            router.push(`/pelanggan/pembayaran?table=${tableNumber}`);
        }, 400);
    };

    return (
        <div className="flex flex-col h-full bg-white">
            <div className="p-6 border-b border-gray-100 bg-white sticky top-0 z-10">
                <div className="flex justify-between items-center mb-4">
                    <h3 className="text-[#D93F21] font-bold text-lg flex items-center gap-2">
                        <Receipt className="w-5 h-5" /> Informasi Pesanan
                    </h3>
                </div>

                <div className="space-y-3">
                    {pendingCart && (
                        <div className="bg-yellow-50 p-3 rounded-lg border border-yellow-100">
                            <p className="text-sm font-bold text-yellow-800 mb-1">Restore item ditemukan</p>
                            <p className="text-xs text-yellow-700 mb-2">Kami menemukan <strong>{pendingCart.length} item</strong> dari sesi sebelumnya.</p>
                            <div className="flex gap-2">
                                <Button size="sm" className="bg-yellow-600 text-white" onClick={restorePendingCart}>Pulihkan</Button>
                                <Button size="sm" variant="outline" onClick={discardPendingCart}>Hapus</Button>
                            </div>
                        </div>
                    )}

                    <div className="bg-gray-50 p-3 rounded-lg border border-gray-100">
                        <p className="text-xs text-gray-400 font-medium mb-1">Pelanggan:</p>
                        <div className="flex items-center gap-2 font-bold text-gray-900 text-sm">
                            <User className="w-4 h-4 text-gray-400" />
                            {user?.name || 'Guest'}
                        </div>
                    </div>
                    <div>
                        <label className="text-xs text-gray-500 font-medium mb-1 block ml-1">Nomor Meja:</label>
                        <Input 
                            type="number" 
                            placeholder="Contoh: 12" 
                            className="font-bold border-gray-200 focus:border-[#D93F21] rounded-lg bg-white h-10"
                            value={tableNumber}
                            onChange={(e) => setTableNumber(e.target.value)}
                        />
                    </div>
                </div>
            </div>

            <div className="flex-1 overflow-y-auto bg-white custom-scrollbar flex flex-col">
                {cart.length > 0 ? (
                    <div className="p-4 space-y-3 pb-4">
                        {cart.map((item) => (
                            <div key={item.id} className="flex items-center gap-3 p-2 rounded-xl border border-gray-50 hover:border-gray-100 transition-colors bg-white shadow-sm">
                                <div className="w-12 h-12 rounded-lg overflow-hidden relative bg-gray-100 shrink-0 border border-gray-100">
                                    <Image src={item.foto_url || ''} alt={item.nama_produk} fill className="object-cover" unoptimized />
                                </div>
                                <div className="flex-1 min-w-0">
                                    <p className="font-bold text-gray-900 text-sm truncate">{item.nama_produk}</p>
                                    <p className="text-xs text-gray-500">{formatRupiah(Number(item.harga_jual))}</p>
                                    {item.catatan && <p className="text-[10px] text-orange-500 italic truncate max-w-[150px]">{item.catatan}</p>}
                                </div>
                                <div className="flex items-center gap-2 bg-gray-50 rounded-lg p-1 border border-gray-100">
                                    <button 
                                        onClick={() => item.qty === 1 ? removeFromCart(item.id) : updateQty(item.id, -1)} 
                                        className="w-6 h-6 flex items-center justify-center bg-white rounded shadow-sm text-gray-500 hover:text-red-500 transition-colors"
                                    >
                                        {item.qty === 1 ? <Trash2 className="w-3 h-3" /> : <Minus className="w-3 h-3" />}
                                    </button>
                                    <span className="text-sm font-bold w-4 text-center">{item.qty}</span>
                                    <button 
                                        onClick={() => updateQty(item.id, 1)} 
                                        className="w-6 h-6 flex items-center justify-center bg-[#D93F21] rounded shadow-sm text-white hover:bg-[#b9351b] transition-colors"
                                    >
                                        <Plus className="w-3 h-3" />
                                    </button>
                                </div>
                            </div>
                        ))}
                    </div>
                ) : (
                    <div className="flex-1 flex flex-col items-center justify-center text-gray-400 text-sm py-10 min-h-[200px]">
                        <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mb-4">
                            <ShoppingCart className="w-8 h-8 text-gray-300" />
                        </div>
                        <h3 className="text-lg font-bold text-gray-600 mb-2">Keranjang Kosong</h3>
                        <p className="text-sm max-w-[200px] text-center">Silakan pilih menu di sebelah kiri untuk mulai memesan.</p>
                    </div>
                )}
            </div>

            {cart.length > 0 && (
                <div className="p-6 bg-gray-50 border-t border-gray-100 sticky bottom-0 z-10 mt-auto">
                    <div className="space-y-2 text-sm mb-6">
                        <div className="flex justify-between text-gray-500">
                            <span>Subtotal ({totalItems} item)</span>
                            <span>{formatRupiah(totalPrice)}</span>
                        </div>
                        <div className="flex justify-between text-gray-500">
                            <span>Pajak (0%)</span>
                            <span>Rp 0</span>
                        </div>
                        <Separator className="my-2" />
                        <div className="flex justify-between font-bold text-gray-900 text-lg">
                            <span>Total</span>
                            <span className="text-[#D93F21]">{formatRupiah(totalPrice)}</span>
                        </div>
                    </div>

                    <Button 
                        className="w-full bg-[#D93F21] hover:bg-[#b9351b] text-white font-bold py-6 rounded-xl shadow-lg shadow-orange-200 transition-all hover:scale-[1.02] active:scale-95"
                        onClick={handleOrderNow}
                        disabled={isProcessing}
                    >
                        {isProcessing ? (
                            <><Loader2 className="w-5 h-5 mr-2 animate-spin" /> Memproses...</>
                        ) : (
                            <>Lanjut Pembayaran <ArrowRight className="w-5 h-5 ml-2" /></>
                        )}
                    </Button>
                </div>
            )}
        </div>
    );
}
