'use client';

import { useEffect, useState, useCallback } from 'react';
import axios from '@/lib/axios';
import { ProdukResto, Kategori } from '@/types';
import { toast } from 'sonner';

export default function useProducts() {
    const [products, setProducts] = useState<ProdukResto[]>([]);
    const [categories, setCategories] = useState<Kategori[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<Error | null>(null);

    const fetchAll = useCallback(async (signal?: AbortSignal) => {
        try {
            setLoading(true);
            setError(null);

            const [resProd, resKat] = await Promise.all([
                axios.get('/api/produkRestos', { signal }),
                axios.get('/api/kategoris', { signal })
            ]);

            setProducts(resProd.data || []);
            setCategories(resKat.data || []);
        } catch (err: any) {
            if (err?.name === 'CanceledError' || err?.message === 'canceled') return;
            console.error(err);
            setError(err);
            toast.error('Gagal memuat menu.');
        } finally {
            setLoading(false);
        }
    }, []);

    useEffect(() => {
        const controller = new AbortController();
        fetchAll(controller.signal);
        return () => controller.abort();
    }, [fetchAll]);

    return {
        products,
        categories,
        loading,
        error,
        refresh: () => fetchAll()
    } as const;
}
